






















import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class Jsfiddle extends Vue {
  get jsfiddleArr(): Array<{
    label: string;
    href: string;
  }> {
    return this.$store.state.jsfiddleArr;
  }
  private activeHref: string = this.jsfiddleArr[0].href;
}
